import React, {Component} from 'react';
import '../App'

class ArtistGallery extends Component{

    constructor(props){
        super(props);
        this.state={
            audio : null,
            playing: false,
            preview_url:''
        }
    }

    playTrack(preview_url){
        let audio = new Audio(preview_url);
        if(!this.state.playing){
            audio.play();
            this.setState({
                playing: true,
                preview_url,
                audio 
            })
        }else{
            if(preview_url === this.state.preview_url){
                this.state.audio.pause();
                this.setState({
                    playing: false
                })
            }else{
                this.state.audio.pause();
                audio.play();
                this.setState({
                    playing: true,
                    preview_url,
                    audio 
                })
            }
        }
    }

    render(){
        const {tracks} = this.props;
        return(
            <div className="Artist-Gallery">
                {
                    tracks.map((track, key)=>{
                        const image_url = track.album.images[0].url;
                        return(
                            <div className="Track" key={key} onClick={()=>this.playTrack(track.preview_url)}>
                                <img 
                                    src={image_url} 
                                    alt="Track"
                                    className="Track-Image"
                                />
                                <div className="Track-Play">
                                    <div className="Track-Play-Inner">
                                        {
                                            this.state.preview_url === track.preview_url
                                                ? <span>| |</span>
                                                : <span>&#9654;</span>
                                        }
                                    </div>
                                </div>
                                <p className="Track-Text">
                                    {track.name}
                                </p>
                            </div>
                        )
                    })
                }
            </div>
        )
    }
}

export default ArtistGallery;


