import React, {Component} from 'react';
import {FormGroup, FormControl, InputGroup, Button} from 'react-bootstrap';
import {spotify} from './Utilities/spofityCredentials.js';
import ArtistProfile from './Components/ArtistProfile.jsx';
import ArtistGallery from './Components/ArtistGallery.jsx';
import './App.css';

class App extends Component{
    constructor(props){
        super(props);
        this.state = {
            query : '',
            access_token : '',
            artist : null,
            tracks: []
        };
    }

    // Function to get access token
    async getAccessToken(client_id, client_secret){
        try{
            fetch('https://accounts.spotify.com/api/token', {
                headers: {
                    'Content-Type' : 'application/x-www-form-urlencoded',
                    'Authorization' : 'Basic ' + btoa(client_id+':'+client_secret)
                },
                body : 'grant_type=client_credentials',
                method : 'POST'
            })
            .then(response => response.json())
            .then(json => {
                this.setState({access_token: json.access_token});
            });
        }catch(error){
            window.alert(error);
        }
    }

    componentDidMount(){
        this.getAccessToken(spotify.client_id, spotify.client_secret);
    }

    UNSAFE_componentWillMount(){
        setInterval(() => {
            this.getAccessToken(spotify.client_id, spotify.client_secret);
        }, 1000);
    }

    //Function to search for the given data
    async searchQuery(){
        const BASE_URL = 'https://api.spotify.com/v1/search?';
        let FETCH_URL = BASE_URL + 'q=' + this.state.query +
                          '&type=artist&limit=1&access_token=' + this.state.access_token;
        const ALBUM_URL = 'https://api.spotify.com/v1/artists';
        await fetch(FETCH_URL,{
            method: 'GET'
        })
        .then(response=>response.json())
        .then(json=>{
            this.setState({artist: json.artists.items[0]});
        });

        FETCH_URL = `${ALBUM_URL}/${this.state.artist.id}/top-tracks?country=US&access_token=${this.state.access_token}`;
        await fetch(FETCH_URL,{
            method: 'GET'
        })
        .then(response=>response.json())
        .then(json=>{
            this.setState({tracks: json.tracks});
        });
    }

    render(){
        let date = new Date();
        return(
            <div className="App">
                <div className="App-title">Live Musick Previewer</div>
                    <FormGroup>
                        <InputGroup>
                            <FormControl
                                onChange = {event=>{
                                    this.setState({query: event.target.value});
                                }}
                                onKeyPress = {event=>{
                                    if (event.key === 'Enter')
                                        this.searchQuery();
                                }}
                                placeholder="Enter Artist name..."
                            />
                            <InputGroup.Append>
                                <Button variant="secondary" className="Button" onClick={()=>this.searchQuery()}><span >Search</span></Button>
                            </InputGroup.Append>
                        </InputGroup>
                        {
                            this.state.artist !== null
                            ?
                            <div>
                                <ArtistProfile artist={this.state.artist}/>
                                <ArtistGallery tracks={this.state.tracks}/>
                            </div>
                            : <div></div>
                        }
                    </FormGroup>
                    <div className="Footer">Jacob Chikwanda &copy; {date.getFullYear()}</div>
            </div>
        )
    }
}

export default App;



