import React, {Component} from 'react';
import '../App.css';

class ArtistProfile extends Component{

    render(){
        let artist = {name:'', followers:{total:''}, images:{url:''}, genres:[]};
        artist = (this.props.artist !== null) ? artist = this.props.artist: artist;
        
        return(
            <div className="Artist-Profile">
                <img className="Artist-Profile-Image" alt="Artist" src={artist.images[0].url}></img>
                
                <div className="Artist-Profile-Information">
                    <div className="Artist-Name">{artist.name}</div>
                    <div className="Artist-Followers">{artist.followers.total} Followers</div>
                    <div className="Artist-Genres">
                        {artist.genres.map((genre, key)=>{
                            genre = genre !== artist.genres[artist.genres.length-1] ? ' '+genre+',' : ' & '+genre;
                            return(
                            <span key={key}>{genre}</span>
                            )
                        })}
                    </div>
                </div>
            </div>
        )
    }
}

export default ArtistProfile;


